.toast {
  position: fixed;
  top: 0px;
  right: 1px;
  z-index: 1000;
}
.toast-box {
  text-align: right;
}
.toast-msg {
  display: inline-block;
  margin-bottom: 10px;
  background: #fff;
  box-shadow: 0 1px 2px rgba(0,0,0,0.15);
  white-space: nowrap;
  padding: 8px 20px;
  margin: 0 auto;
}
.toast-msg span {
  display: inline-block;
  vertical-align: middle;
}
.toast-msg .iconfont {
  font-size: 18px;
  vertical-align: middle;
}
.toast-msg-success,
.toast-msg-error,
.toast-msg-info {
  color: #fff;
}
.toast-msg-success {
  background: #5dda66;
}
.toast-msg-error {
  background: #f00;
}
.toast-msg-info {
  background: #fffdb4;
  color: #737373;
}
.toast-box {
  -webkit-transition: opacity 0.3s ease, -webkit-transform 0.3s ease;
  transition: opacity 0.3s ease, -webkit-transform 0.3s ease;
  transition: transform 0.3s ease, opacity 0.3s ease;
  transition: transform 0.3s ease, opacity 0.3s ease, -webkit-transform 0.3s ease;
}
.toast-box-enter,
.toast-box-leave {
  opacity: 0;
}
.toast-box-enter {
  -webkit-transform: translate3d(0, -30px, 0);
          transform: translate3d(0, -30px, 0);
}
.toast-box-leave {
  -webkit-transform: translate3d(0, -30px, 0);
          transform: translate3d(0, -30px, 0);
}


